.deptlist-container {
    width: 100%;
    height: 100%;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    background-color: #222222;
}
.bgimg{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    position: absolute;
    z-index: -99;
}
.icon-container {
    -ms-flex: 8 1;
        flex: 8 1;
    padding-top:30vh; 
    margin:0 20vw;;
    display: -ms-flexbox;;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.main-icon {
    -ms-flex: 1 1;
        flex: 1 1;
    height:50vw;
    width: 50vw;
    max-height:50vw;
    max-width: 50vw;
    cursor: pointer;
}
.side-icon {
    -ms-flex: 1 1;
        flex: 1 1;
    display: none;
}
.circle {
    width: 100%;
    height: 100%;
    /* background-color: black; */
    border-radius: 50%;
}
.dep_name-container {
    -ms-flex: 5 1;
        flex: 5 1;
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
}
.dep_name {
    margin: auto;
    padding: 0 10%;
    font-size: 2.5em;
    font-family: 'Cuprum', sans-serif;
    cursor: pointer;
    color: white;
}
.pagination {
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    vertical-align: bottom;
    -ms-flex-pack: center;
        justify-content: center;
    padding-top: 5vh;
    padding-bottom: 3vh;
}
.pagination-icon .dep_name {
    display: none;
}
.pagination-icon {
    margin: 0 auto;
    margin-top: auto;
}
.pagination-icon .circle {
    margin-top: 0;
    -ms-flex: 1 1;
        flex: 1 1;
    height:7vw;
    width: 7vw;
    max-height:7vw;
    max-width: 7vw;
}
.active .circle {
    height:9vw;
    width: 9vw;
    max-height:9vw;
    max-width: 9vw;
}
.bot-icon {
    width: 100%;
    /* background-color: white; */
    border-radius: 50%;
}



