.eventlist-container {
    width: 100%;
    height: 100%;
    position: relative;
    color: black;
    background-color: #222222;
}
.bgimg{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    position: absolute;
    z-index: -99;
}
.event_list {
    overflow-x: hidden;
    overflow-y: auto;
    padding: 1em 0.3em;
    padding-top: 10%;
    font-family: "Gtek Technology" , sans-serif;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100%;
}
.event {
    text-align: center;
    background-color: #e5e5e5;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
            box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    padding: 5% 0;
    margin: 1%;
    margin-bottom: 2vw;
    border-radius: 5px;
}
.event:hover {
    -webkit-box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
            box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}
.event_logo {
    display: none;
}
.event_name {
    padding: 2px 16px;
}
@media screen and (min-width:768px) {
    .dept_name {
        font-size: 4em;
    }
    .event_list {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
    }
    .event {
        -ms-flex: 1 1;
            flex: 1 1;
        width: 30vw;
        min-width: 30vw;
        max-width: 30vw;
    }
    .event_logo,.event_logo img {
        display: inline-block;
        max-width: 100%;
    }
    .dept_logo {
        height: 15vw;
        width: 15vw;
    }
}


